import React from "react";
import { NavLink } from "react-router-dom";

const Common=(props)=>{
    return(
        <>
        <section id="header" className="d-flex align-items-center">
        <div className="container-fluid ">
            <div className="row">
                <div className="col-10 mx-auto">
                <div className="row">
                    <div className="col-md-6 pt-5 pt-lg-0 or order-2 order-lg-1 d-flex justify-content-center flex-column">
                        <h1> {props.name} <strong className="brand-name"> By Pooja </strong></h1>
                        <h2 className="my-3">
                           Increment Decrement Reset
                        </h2>
                        <div className="mt-3">
                            <NavLink to={props.visit} className="btn-get-started"> {props.btname} </NavLink>
                        </div>
                        <p> {props.description}</p>
                    </div>
                    <div className="col-lg-6 order-1 order-lg-2 header-img">
                        <img src={props.imgsrc} className="img-fluid animated" alt="home img" style={{width:"350px",height:"300px"}}/>
                    </div>
                </div>
                </div>
            </div>
        </div>    
        </section>
        
        </>
    )
}
export default Common;